body {
    background-image: url(/Calculator/static/media/bgtexture.cd5160b8.jpg);
    background-repeat: repeat;
    margin: 0px;
}

#root {
    position: absolute;
    height: 100%;
    width: 100%;
}

#calc-body {
    display: inline-block;
    width: 375px;
    height: 375px;
    background-color: grey;
    position: relative;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 25px;
    padding: 15px;
}

/* Display style */
#display {
    display: block;
    padding: 15px 5px;
    background-color: wheat;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    margin: 5px 9px 5px 3px;
}

#txt {
    margin: 0px;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    text-align: right;
}
.button-cont {
    padding: 15px;
    color: white;
    font-size: 18px;
    border: 2px solid darkgrey;
    text-align: center;
    margin: 3px;
    -webkit-transition: 0.1s;
    transition: 0.1s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
    float: left;
    font-family: sans-serif;
}

.button-cont:hover {
    cursor: pointer;
    border-color: lightgray;
}

.button-cont:active {
    background-color: darkgrey;
}

.b-3 {
    width: 23%;
}

.b-2 {
    width: 47.5%;
}
